#stack{
  margin-left: 50px;
}
img{
  width:1200px;
  height:800px;
}

fieldset {
  width: 2000px;
  margin-left: 50px;
  font-size: 1.5em;
  font-family: 'Nanum gothic', sans-serif;
}

.title {
  text-align: center;
  color: #333;
  font-size: 10px;
  font-weight: bold;
  margin-bottom: 20px;
  
}

.graph {
  width: 100px;
  height: 200px;
  float: left;
  margin-left: 10px;
  perspective: 800px;
  position: relative;
}

.bar {
  width: 80px;
  height: 100%;
  margin: 0 auto;
  background-color: #3498db;
  transform-style: preserve-3d;
  transform-origin: center bottom;
  animation: rotate 10s linear infinite;
  position: relative;
  overflow: hidden;
}

.filled {
  width: 150%;
  height: 100%;
  background-color: white;
  position: absolute;
  bottom: 0;
  left: 0;
  transform-origin: center bottom;
}

.label {
  text-align: center;
  color: #333;
  font-size: 10px;
  font-weight: bold;
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
}

ul {
  list-style: none;
  padding: 0;
  float: left;
  margin: 0;
}

li {
  margin-bottom: 20px;
}

legend {
  font-size: 1.5em;
  margin-bottom: 20px;
}

