@import url('https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@700&display=swap');
@import "reset.css";

header {
  font-size: 25px;
  text-align: center;
  margin-bottom: 50px;
  font-family: 'Nanum gothic', sans-serif;
}
header a {
  text-decoration: underline; /* 링크 텍스트에 밑줄 추가 */
  color: skyblue; /* 링크 텍스트 색상 설정 */
  cursor: pointer; /* 마우스 커서를 포인터로 변경하여 클릭 가능함을 나타냅니다 */
}

h1 {
  font-size: 72px;
}

footer {
  margin-top: 20px;
  text-align: center;
  background-color: black;
  color: white;
  font-size: 15px;
  font-family: 'Nanum gothic', sans-serif;
  font-style: normal;
}

main{
  margin-left:20px;
  background-color: #f1f1f1;
  font-size: 15px;
  font-family: 'Nanum gothic', sans-serif;
}

body {
  background-color: #f1f1f1;
  line-height: 1.5;
  font-weight: bolder;
  font-family: 'Nanum gothic', sans-serif;
  }

  .container {
    display: grid;
    grid-template-columns: 100px 1fr; 
  }

  button {
    background-color: #555;
    color: white;
    padding: 8px 16px;
    border: none;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 4px;
    width: 150px;
  }
  
  button:hover {
    background-color: #333;
  }
  
  /* .nav와 버튼 스타일 조합 */
  nav button {
    display: block;
    color: #000;
    text-decoration: none;
    width: 100%;
    border: none;
  }
  
  .nav .button.active {
    background-color: #555;
    color: white;
  }
  
  .nav .button:hover:not(.active) {
    background-color: #555;
    color: white;
  }
  