.nav {
    list-style-type: none;
    margin: 0;
    padding: 0;
    width: 25%;
    background-color: #f1f1f1;
    position: fixed;
    height: 100%;
    overflow: auto;
  }
  
  .nav-link {
    display: block;
    color: #000;
    padding: 8px 16px;
    text-decoration: none;
  }
  
  .nav-link.active {
    background-color: #555;
    color: white;
  }
  
  .nav-link:hover:not(.active) {
    background-color: #555;
    color: white;
  }