#project{
  margin-left: 50px;
}
img{
  width:1200px;
  height:800px;
}

fieldset {
  width: 2000px;
  margin-left: 50px;
  font-size: 1.5em;
  font-family: 'Nanum gothic', sans-serif;
}

.dropbtn {
  font-family: 'Nanum gothic', sans-serif;
}

.dropdown {
  
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  min-width: 160px;
  z-index: 1;
}

.dropdown:hover .dropdown-content {
  display: block;
}